.container{
  display: flex;
  align-items: center;

  .img{
    width: 3rem;
    height: 3rem;
    transform: rotate(-45deg);
    & * {
      fill: #FF868E;
    }
  }
  span{
    display: inline-block;
    margin-left: .5rem;
    font-family: 'Jost', sans-serif;
    font-weight: 500;
    font-size: 2rem;
  }
}